  @Prop({ default: () => ({}) })
  private headerMods!: Hash;

  @Prop({ default: () => ({}) })
  private bodyMods!: Hash;

  @Prop({ default: () => ({}) })
  private footerMods!: Hash;

  private blockName: string = 'layout';
}
