




import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class Icon extends Vue {
  @Prop({ default: () => ({}) })
  private mods!: Hash;

  @Prop({ default: () => 'mdi' })
  private library!: string;

  @Prop({ default: (): string => null })
  private name!: string;

  @Prop({ default: () => 24 })
  private size!: number;

  private blockName: string = 'icon';

}
